.weather-box {
  width: 40%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px 16px 16px 16px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
  margin: 2%;
}

.show {
  display: block;
}

.hide {
  display: none;
}

@media only screen and (max-width: 600px) {
  .weather-box {
    width: 80%;
  }

  h1 {
    font-size: 8.5vw;
  }
  h2 {
    font-size: 6vw;
  }
  h3 {
    font-size: 4vw;
  }
}
